  .collection_subscription {
    padding-left: 15px;
    cursor: pointer;
    display: flex;
    align-items: center;
    gap: 15px;
    margin-bottom: 16px;
    height: 50px;
    margin-bottom: 10px;
    color: white;
    font-size: 16px;
    font-weight: 400;
    background-color: #454D55;
    border: none;

  }

  .collection_subscription:last-child {
    margin-bottom: 0;
  }

  .collection_subscription_avatar {
    border-radius: 24px;
    width: 48px;
    height: 48px;
  }

