.container {
  position: relative;
  margin: 0 auto;
  padding: 20px 15px 29px 15px;
  max-width: 780px;
}

.tonConnectButton {
  width: 100% !important;
  display: flex;
  flex-direction: column;
  margin-bottom: 57px;
}

.tonConnectButton button {
  text-transform: lowercase;
  background-color: #449BF1;
  justify-content: center;
  border-radius: 15px;
  padding: 16px;
  width: 100%;
  height: 50px;

}

.tonConnectButton button div {
  font-size: 20px;
  font-weight: 700 !important;
}

.tonConnectButton svg {
    display: none;
}

.event-box {
  cursor: pointer;
  display: flex;
  align-items: center;
  height: 100%;
  border-radius: 15px;
  padding: 9px 7px 23px 16px;
  font-size: 96px;
  font-weight: 700;
  text-transform: lowercase;
  background-size: 100% 100%;
  background-image: url("img/eventBackgroundDark.jpg");
  background-position: left bottom;
  background-repeat: no-repeat;
}

.btn {
  outline: none;
  padding: 10px;
  border-radius: 15px;
  cursor: pointer;
  border: none;
  color: white;
  font-weight: 600;
  font-size: 24px;
  text-align: center;
  background: #449BF1;
}

.buttons-box {
  display: flex;
  gap: 17px;
  margin-bottom: 16px;
}

.link-button {
  cursor: pointer;
  display: flex;
  height: 100%;
  justify-content: center;
  align-items: center;
  padding: 15px;
  border-radius: 15px;
  border: 2px solid #449BF1;
  font-size: 32px;
  font-weight: 700;
  text-transform: lowercase;
}



.grow {
  flex-grow: 1;
}

.media-container {
  display: flex;
  gap: 5px;
  justify-content: space-between;
  align-items: center;
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 10px;
}

.media-container .our-media {
}

.media-container .github {  
  color: #449BF1;
}

.media-container .docs {
  color: #449BF1;
}

.arrow {
  display: block;
  cursor: pointer;
  width: 55px;
  height: 41px;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  background-color: transparent;
  border: none;
}

.arrow-box {
  display: flex;
  justify-content: space-between;
  margin-bottom: 17px;
  width: 100%;
}

.arrow-back {
  background-image: url("./img/backArrow.svg");
}

.arrow-next {
  background-image: url("./img/nextArrow.svg");
}


.collection_image_container {

  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.collection_image_container.info {

  overflow: hidden;
  border-radius: 13px;
  width: 137px;
  height: 137px;
  min-width: 137px;
  border: none;
  background-color: #2D3444;
}

.collection_image_container .img {
  min-width: 200px;
  min-height: 200px;
  max-width: max-content;
}

.collection_image_container.info .img {
  min-width: 137px;
  min-height: 137px;
  max-width: max-content;
}

.input-btn {
  width: max-content;
}

.form-item-label {
  display: inline-block;
  margin-bottom: 3px;
  padding-left: 10px;
  color: #449BF1;
  font-weight: 600;
  font-size: 16px;
}



.form-input {
  display: block;
  padding: 13px 10px;
  border-radius: 15px;
  height: 53px;
  width: 100%;
  color: white;
  font-weight: 400;
  font-size: 20px;
  background-color: #2D3444;
  border: none;

}

.div-for-input {
  display: block;
  border: 2px solid #449BF1;
  border-radius: 15px;
  height: 53px;
  width: 100%;
}

.form-input[type="datetime-local"]::-webkit-inner-spin-button,
.form-input[type="datetime-local"]::-webkit-calendar-picker-indicator {
    display: none; 
}


.form-input:focus {
  outline: none !important;
  color: white;
  background-color: #2D3444;
}
