.input-checkbox {
  display: flex;
  align-items: center;
  height: 30px;
  padding-right: 40px;
  cursor: pointer;
  color: #449BF1;
  font-weight: 600;
  font-size: 20px;
  background-image: url('./img/inputInactive.svg');
  background-position: right center;
  background-size: 30px 30px;
  background-repeat: no-repeat;
}

.input-checkbox.checked {
  color: white;
  background-image: url('./img/inputActive.svg');
}

.square-btn {
  position: absolute;
  z-index: 10;
  right: 10px;
  cursor: pointer;
  border-radius: 10px;
  bottom: 11.5px;
  width: 30px;
  height: 30px;
  border: 2px solid #449BF1;
  
}

.square-btn.active {
  background-color: #449BF1;
}




.with-square {
  padding-right: 50px;
  padding-left: 7px;
  background-image: url('./img/inputInactive.svg');
  background-position: calc(100% - 10px) center;
  background-size: 30px 30px;
  background-repeat: no-repeat;
}

.with-square:focus, .with-square:valid, .with-square.active {
  background-image: url('./img/inputActive.svg');
}

.with-square.subscriptions_initial {
  background-image: url('./img/inputInactive.svg') !important;
}

.form-box {
  margin-bottom: 17px;
}

.form-item {
  display: block;
  margin-bottom: 5px;
}

.dataLoading {
  margin-bottom: 30px;
}

