.collection_description_container {  
 
  display: flex;
  margin: 0 auto 17px;
  gap: 15px;
    
}


.collection_summary {
  color: #449BF1;
  font-weight: 600;
  word-break: break-word;
}
.collection_title {
  font-size: 24px;
  color: white;
  margin-bottom: 7px;
 

}

.collection_description {
  font-size: 20px;
  margin-bottom: 10px;
}

.calendar-invite-container {
  margin-bottom: 15px;

}

.calendar-invite-container .start-date {
  margin-bottom: 7px;
  vertical-align: middle;
  width: 68px;
  height: 36px;
  display: inline-block;
  padding-top: 12px;
  text-align: center;
  margin-right: 14px;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  background-color: transparent;
  background-image: url("./img/calendar.svg");
}




.calendar-invite-container .invites {
  vertical-align: middle;
  height: 24px;
  width: 33px;
  padding-top: 7px;
  padding-left: 25px;
  display: inline-block;
  background-position: left;
  background-size: contain;
  background-repeat: no-repeat;
  background-color: transparent;
  background-image: url("./img/human.svg");
}

.submit-btn { 
  display: block;
  cursor: pointer;
  margin: 0;
  padding: 13px 10px;
  border: 2px solid #449BF1;
  border-radius: 15px;
  width: 100%;
  color: white;
  text-align: center;  
  font-size: 24px;  
  font-weight: 600;
  text-transform: lowercase;
  background-color: transparent;
}

.submit-btn.is-connect {   
  border: none;
  background-color: #449BF1;
}

.claim-button {
  
}



.countdown-timer {
  margin-bottom: 24px;
  text-align: center;
  font-weight: 600;
  font-size: 24px;
 
}

.countdown-timer .timer {
  color: white;
}

.countdown-timer .label {
  color: #449BF1;
  margin-bottom: 10px;
}



.subscriptions-input-box .form-input.new-input {
  background-color: #2D3444;
  border: none;
}


.subscriptions_initial {
  color: #6779A6;
  font-style: italic;
  font-size: 14px;
  height: 53px;
}

.default-subscription {
  color: white;
}

.subscriptions-input-box .form-input {
  height: 53px;
  background-color: #2D5E8E;
  font-weight: 400;
}

.no-select {
  outline: none;
  user-select: none; 
  -webkit-user-select: none; 
  -moz-user-select: none; 
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  -webkit-tap-highlight-color: transparent;
}

.connect-message {
  margin-bottom: 30px;
  text-align: center;
}

.popup-window {
  /* position: sticky;
  top: 0;
  z-index: 100; */
  margin-bottom: 10px;
 
  border-radius: 15px;
  padding: 10px;
  background-color: #449BF1;
}

.popup-errors-box {
  position: fixed;
  top: 0;
  z-index: 100;
  width: 100%;
  margin: 0 auto;
  padding: 10px;
}

.wallet-pop-up {
  position: absolute;

}


.popup-message {
  font-size: 20px;
  font-weight: 600;
  margin: 0 auto;
  color: rgb(var(--background-primary));
  text-align: center;
}

.home-container {
  position: relative
}

.for-button-click:active {
  padding: 5px;
}

.for-button-click.event-btn {
  height: 360px;
  margin-bottom: 17px;
}

.for-button-click.link-btn {
  height: 70.8px;
}

.main {
  position: relative;
}

.subscription-input {
  padding-left: 15px;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 16px;
  height: 50px;
  margin-bottom: 10px;
  color: white;
  font-size: 16px;
  font-weight: 400;
  background-color: #454D55;
  border: none;

}

.readonly-input, .readonly-input:focus, .input-count-btn.readonly-input, .input-count-btn.readonly-input:focus {
  color: #898e92;
  cursor: not-allowed;
}

