.modal-window {
  max-width: 500px;
  padding: 20px;
  border-radius: 15px;
  border: 2px solid #449BF1;
  color: rgb(var(--accent));
  font-weight: 600;
  font-size: 24px;
  text-align: center;
  background: rgb(var(--background-primary));
}




.modal-overlay {
  position: fixed;
  z-index: 500;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 15px;
  background-color: rgba(23, 33, 43, 0.71);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-overlay.blurred {
  backdrop-filter: blur(5px);
  z-index: 8000;
}

.modal-buttons-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
}

.modal-button {
  padding: 10px;
  border-radius: 15px;
  width: 50%;
  cursor: pointer;
  border: none;
  color: white;
  font-weight: 600;
  font-size: 20px;
  text-align: center;
  background: #449BF1;
}

.modal-message {
  margin-bottom: 20px;
  color: #449BF1;
  font-size: 20px;
}

.modal-button.leave {
  background-color: #FF370B;
}

.popup-window-error {
  background-color: #FF370B;
  border: none ;
}

.popup-window-error .popup-message, .popup-window-error .popup-close {
  color: white;
}

