.popup-flex {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.popup-close {
  cursor: pointer;
  color: rgb(var(--background-primary));
  font-weight: 600;
  font-size: 20px;
  padding-bottom: 3px;
  border: 2px solid;
  height: 25px;
  width: 25px;
  min-width: 25px;
  max-width: 25px;
  border-radius: 50%;
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;  
}

.get-btn-inactive {
  cursor: not-allowed
}

.overlay-window {
  position: fixed; /* Зафиксировать на экране */
  top: 0;
  left: 0;
  width: 100%; /* На весь экран */
  height: 100%; /* На весь экран */
  padding: 10px;
  background-color: rgba(23, 33, 43, 0.71);
  color: white; /* Цвет текста */
  display: flex; /* Использовать Flexbox для центрирования */
  align-items: center; /* Вертикальное центрирование */
  justify-content: center; /* Горизонтальное центрирование */
  z-index: 7000; /* Перекрывает все другие элементы */
}

.overlay-window-message {
  height: 71px;
  padding-left: 82px;
  display: flex;
  align-items: center;
  font-size: 24px;
  font-weight: 600;
  background-image: url('./img/timer.svg');
  background-position: left center;
  background-size: 71px 71px;
  background-repeat: no-repeat;
}

.input-apply-btn {
  
  width: 100%;
  height: 50px;
  background-color: #449BF1;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-weight: 600;
  font-size: 24px;
}

.fixed-apply-btn-box {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 7px;
}
