.header {
    position: relative;
    display: flex;
    justify-content: space-between;
    margin-bottom: 40px;
}

.logo {
    
}

.react-switch-bg {
    border: 2px solid;
}

.react-switch-off .react-switch-bg {
    border-color: #449BF1;
    cursor: pointer !important;
}

.react-switch-on .react-switch-bg {
    /* border-color: white; */
    border-color: rgb(var(--background-primary));
}

.react-switch-handle {
    

}

.react-switch-off {
    color: #449BF1;
    background: transparent;
    font-weight: 400;
}

.react-switch-on {
    cursor: pointer;
    color: rgb(var(--background-primary));
    background: white;
    font-weight: 600;
}

.react-switch-container {
    position: relative;
    cursor: pointer;
    height: 40px;
    display: flex;
    align-items: center;
    gap: 10px;
    border-radius: 15px;
    padding-right: 10px;
    font-size: 14px;
}

.popup-menu {   
    position: absolute;
    right: 0;
    top: 45px;
    z-index: 10;
    color: black;
    border: 1px solid #ccc; 
    border-radius: 4px; 
    padding: 10px;
    background: white;    
}

.popup-menu-item {
    cursor: pointer;
    padding: 5px
}

.react-switch {
    opacity: 1 !important;
    padding-left: 5px;
    cursor: pointer;
}

.logo {   
    display: flex;
    align-items: center;
    gap: 10px;
    font-weight: 500;
}

.wallet-disconnect-menu {
    cursor: pointer;
    display: flex;
    align-items: center;
    height: 100%;

}

.disconnect-menu-disabled {
    cursor: default
}