@font-face {
  font-family: 'Inter';
  src: local('Inter Regular'), local('Inter-Regular')
      url('fonts/Inter-Regular.woff2') format('woff2'),
      url('fonts/Inter-Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Inter';
  src: local('Inter Light BETA'),
      url('fonts/Inter-LightBETA.woff2') format('woff2'),
      url('fonts/Inter-LightBETA.woff') format('woff');
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: 'Inter';
  src: local('Inter Semi Bold'), local('Inter-SemiBold'),
      url('fonts/Inter-SemiBold.woff2') format('woff2'),
      url('fonts/Inter-SemiBold.woff') format('woff');
  font-weight: 600;
  font-style: normal;
}

@font-face {
  font-family: 'Inter';
  src: local('Inter Bold'), local('Inter-Bold'),
      url('fonts/Inter-Bold.woff2') format('woff2'),
      url('fonts/Inter-Bold.woff') format('woff');
  font-weight: bold;
  font-style: normal;
}
@font-face {
  font-family: 'Inter';
  src: local('Inter Medium'), local('Inter-Medium'),
      url('fonts/Inter-Medium.woff2') format('woff2'),
      url('fonts/Inter-Medium.woff') format('woff');
  font-weight: 500;
  font-style: normal;
}

:root {
  /* light theme tokens */
  --background-primary-light: 255, 255, 255;
  --accent-light: 41, 48, 64;

  /* dark theme tokens */
  --background-primary-dark: 23, 33, 43;
  --accent-dark: 255, 255, 255;
}

/* map tokens to proper theme */
[data-theme='light'] {
  --background-primary: var(--background-primary-light);
  --accent: var(--accent-light);
}

[data-theme='dark'] {
  --background-primary: var(--background-primary-dark);
  --accent: var(--accent-dark);
}

html {
	box-sizing: border-box;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

a {
	color: inherit;
	text-decoration: none;
}

img {
	max-width: 100%;
}

h1, p {
  margin: 0;
}

body {
  min-height: 100vh;
  margin: 0;
  font-family: 'Inter', 'font', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: rgb(var(--accent));
  background-color: rgb(var(--background-primary));
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
